import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { logout } from '../../actions/api/auth';
import { notifySuccess } from '../../actions/notification';

class Logout extends Component {
  componentWillMount() {
    const { uid, client, token } = this.props;
    this.props.logout(uid, client, token);
    this.props.notifySuccess('You logged out succesfully');
  }

  render() {
    return <Redirect to="/login" />;
  }
}

Logout.propTypes = {
  logout: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  uid: PropTypes.string.isRequired,
  client: PropTypes.string.isRequired,
  token: PropTypes.string.isRequired,
};

function mapStateToProps(state) {
  return {
    uid: state.auth.uid,
    client: state.auth.client,
    token: state.auth.token,
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ logout, notifySuccess }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Logout);
